#loading {
  @include flex($justify: center);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--mainBg-color);
  z-index: 100000;
  h3 {
    color: var(--main-color);
    font-size: 140px;
    font-variation-settings: "wght" 800;
  }
}
